.hero_section {
  
    background: url('../../assets/images/img_bg_page_title.jpg');
    background-repeat: no-repeat;
    
}
.hero_content,
.hero_img{
    margin-top: 60px;
}
.hero_content h2 {
    line-height: 55px;
    font-size: 2.4rem;

}

.hero_content h2,
p {

    color: #fff;
}

.hero_content h2 span {
    background: linear-gradient(-45deg, #e250e5, #4b50e6, #e250e5, #4b50e6);
    background-size: 100% 100%;
    background-clip: text;
    -webkit-text-stroke: 3px transparent;
    -webkit-text-fill-color: #fff;
    margin-left: 7px !important;
    display: inline-block;
}

.hero_btns {
    margin-top: 30px;
}

.hero_btns .explore_btn,
.create_btn {
    border: none;
    outline: none;
    padding: 7px 25px !important;
    border-radius: 50px;
    border: 1px solid #5142fc;
    font-size: 0.9rem;
    background: transparent;
}

.explore_btn i,
.create_btn i {
    color: #fff;
    font-size: 1rem;
}

.explore_btn a,
.create_btn a {
    text-decoration: none;
    color: #fff;
}

.hero_img {
    width: 500px;
    margin-left: auto;
}

.hero_img img {
    border-radius: 7px;
}

@media only screen and (max-width:991px) {
    .hero_img {
        width: 100%;
        margin: auto;
        margin-top:20px ;
    }
    .hero_content h2{
        font-size: 1.5rem;
        line-height: 40px;
    }
    .hero_content p {
        font-size: 0.8rem;
        line-height: 25px;
    }
    .create_btn,
    .explore_btn {
        padding: 4px 25px !important;
    }
    .create_btn a,
    .explore_btn a{
        font-size: 0.7rem;
    }
}
@media only screen and (max-width:576px) {
    .hero_img{
        width: 100%;
        margin: auto;
        margin-top:50px ;
    }
}
