.header{
    width: 100%;
    height: 70px;
    line-height: 70px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 11111111;
    /* background: url('../../assets/images/img_bg_page_title.jpg') no-repeat; */

}
.navigation{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header_shrink{
    background: #14141f;
    box-shadow: 3px 3px 4px -3px #E250E5 ;
}
.nav_list{
 display: flex;
 align-items: center;
 column-gap: 2.7rem;
 margin-bottom: 0;
}
.nav_item a{
    text-decoration: none;
    color: #fff;
}
.nav_item a:hover{
    color: #fff;
}
.active{
    color: #e250e5 !important;
}
.logo h2{
    background:linear-gradient(-45deg, #E250E5,#4B50E6,#E250E5,#4B50E6);
    background-size:100% 100%; 
    background-clip:text;
    -webkit-text-stroke: 3px transparent;
   -webkit-text-fill-color: #14141f;  
   margin-bottom: 0;
}
.nav_right .btn{
    padding: 7px 25px;
    border: 1px solid #5142FC;
    border-radius: 50px;
}
.nav_right .btn a{
    color: #fff;
    text-decoration: none;
    font-size: 0.8rem;
    padding: 3px ;
}
.nav_right .btn span i{
    color: #fff;
    padding: 3px;
}
.nav_right .btn a:hover{
    color: #fff;
}
.mobile_menu{
    color: #fff;
    font-size:1.3rem ;
    display: none;
}
@media only screen and (max-width:992px){
    .nav_list{
        display: flex;
        align-items: center;
        column-gap: 0.7rem;
        margin-bottom: 0;
       }
}
@media only screen and (max-width:768px) {
    .nav_menu{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(#14141f83,#14141f83);
        z-index: 222222;
        display: none;
    }
    .nav_right{
        padding: 10px;
    }
    .active_menu{
        display: block;
    }
    .nav_list{
        position: absolute;
        top: 0;
        right: 0;
        width:250px ;
        height: 100%;
        background: #343444;
        z-index: 333333;
        flex-direction: column;
        padding-top: 40px;
        line-height: 40px;
    }
    .mobile_menu{
        display: block;
    }
    .btn{
        padding: 4px 25px !important;
    }
    .btn a{
        font-size: 0.7rem !important;
    }
}
@media only screen and (max-width:280px){
    h2.d-flex.gap-2.align-items-center{
        font-size: 20px;
    }  
}