.modal_wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #14141f9c;
    z-index: 444444;
}
.single_modal{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: 500px;
    width: 500px;
    background: #fff;
    border-radius: 5px;
    padding: 30px;
    z-index: 5555555;
    cursor: pointer;
}
.single_modal h6{
    font-size: 1.1rem;
    font-weight: 500;
    color: black;
    margin-bottom: 10px;
} 
.single_modal .money{
    color: black;
    font-weight: 500;
    font-size: 1.1rem;
}
.input_item input{
    width: 100%;
    border:1px solid #14141f9c;
    background: transparent;
    color: #14141f9c;
    padding: 9px 25px;
    border-radius: 5px;
    outline: none;
    font-size: 0.8rem;
} 
.input_item h6{
    font-size: .9rem;
}
.place_bid-btn{
    padding: 7px 25px;
    border: none;
    outline: none;
    width: 120px;
    margin: auto;
    background: #5142fc;
    color: #fff;
    border-radius: 50px;
    font-size: 0.8rem; 
    display: block;
    margin-top: 15px;
}
.close_modal{
    position: absolute;
    top: -2px;
    right: -2px;
    z-index: 666666;
}
.close_modal i{
    padding: 5px;
    background: #5142fc;
    color: #fff;
    border-radius: 50%;
}
@media only screen and (max-width:992px) {
    .single_modal{
        width: 300px;
        height: auto;
    }
    .single_modal h6{
        font-size: .9rem;
    }
    .single_modal p{
        font-size: 0.8rem;
        margin-bottom: 0;
    }
    .money{
        font-size: 0.8rem !important;
    }
    .input_item h6{
        font-size: .7rem;
