.all_category_filter select,
.all_items_filter select,
.filter_right select {
    padding: 8px 25px;
    font-size: .9rem;
    color: #fff;
    cursor: pointer;
    border: none;
    outline: none;
    background: #343444;
    border-radius: 5px;
}

@media only screen and (max-width:786px) {

    .all_category_filter select,
    .all_items_filter select,
    .filter_right select {
        padding: 5px 20px;
        font-size: 0.6rem;
    }
}
@media only screen and (max-width:320px) {

    .all_category_filter select,
    .all_items_filter select,
    .filter_right select {
        padding: 5px 0px;
        font-size: 0.6rem;
    }
}